export const mixin = {
  methods: {
    //提示信息
    notify(title,type){
      this.$notify({
        title: title,
        type: type
      })
    },

    //根据相对地址获取绝对地址
    getUrl(url){
      return `${this.$store.state.HOST}/${url}`
    },

    //上传前进行校验
    beforeDocumentUpload(file){
      const isPic = (file.type === 'csv') || (file.type === 'csv');
      if(!isPic){
        this.$message.error("只能上传csv格式的文件！");
        return false;
      }
      return true;
    },

    //上传成功后的操作
    handleDocumentSuccess(res){
      if(res.code == 1){
        this.selectAll(); //刷新
        this.notify("上传成功","success");
      }else{
        this.notify("上传失败","error");
      }
    },


  }
}



