<template>
  <div>
    <el-row :gutter="20" class="mgb20">
      <!--共24，故可分2列-->
      <el-col :span="12">
        <el-card>
          <div class="grid-content">
            <div class="grid-cont-center">
              <div><i class="el-icon-document"></i></div>
              <br>
              <div style="font-size: 20px">单个企业分类</div>
              <div style="text-align: left">
                通过手动输入或csv文件上传单个企业信息，我们将给出企业是否可能成为僵尸企业的结果，并给出可视化分析报告
              </div>
              <el-upload :action="handle" :before-upload="beforeDocumentUpload" :on-success="handleDocumentSuccess">
                <el-button size="mini" type="primary">
                  <i class="el-icon-upload" style="font-size: 14px"></i>
                  上传文件
                </el-button>
              </el-upload>
            </div>
          </div>
        </el-card>
      </el-col>

      <el-col :span="12">
        <el-card>
          <div class="grid-content">
            <div class="grid-cont-center">
              <div><i class="el-icon-document-copy"></i></div>
              <br>
              <div style="font-size: 20px">批量企业分类</div>
              <div style="text-align: left">
                通过手动输入或csv文件批量上传企业信息，其中包含每个企业的ID以及是否可能成为僵尸企业的结果
              </div>
              <br>
              <el-upload :action="handle" :before-upload="beforeDocumentUpload" :on-success="handleDocumentSuccess">
                <el-button size="mini" type="primary">
                  <i class="el-icon-upload" style="font-size: 14px"></i>
                  上传文件
                </el-button>
              </el-upload>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import {mixin} from "../mixins";

    export default {
        name: "Main",
        mixins: [mixin],
        methods: {
          handle(){

          },
        }
    }
</script>

<style scoped>
  .grid-content {
    display: flex;
    align-items: center;/*垂直方向居中*/
    height: 200px;
  }

  .grid-cont-center {
    flex: 1;
    text-align: center;/*水平方向居中*/
    font-size: 14px;
    color: #a9a9a9;
  }

  .grid-num {
    font-size: 30px;
    font-weight: bold;
  }

  .el-icon-document {
    font-size: 70px;
    color: #87cefa;
  }

  .el-icon-document-copy {
    font-size: 70px;
    color: #87cefa;
  }
</style>



